<script>
export default {
  name: "home",
  data() {
    return {
      screenWidth: window.screen.availWidth, // 屏幕宽
      screeHeight: window.screen.availHeight, // 屏幕高
    };
  },
  methods:{
	SaveDataAndGo(qrCodeProcessData){
		var this_=this;
		var p=window.parent;
  if(p){

   
    
      if(qrCodeProcessData){
        var app=p.app;
        if(app){
          var DbUtil=app.DbUtil;
          var DoorMasterSdkUtil=app.DoorMasterSdkUtil;
          if(DbUtil){
            DbUtil.saveQrCodeData(p,qrCodeProcessData,function(qrCodeIds){
            console.log("1114")
              if(qrCodeIds&&Array.isArray(qrCodeIds)){
                
                    
                var BtnList = qrCodeProcessData.BtnList;
                if(BtnList&&Array.isArray(BtnList)){
                  var l=BtnList.length;
                  if(l==1){
                      if(DoorMasterSdkUtil){
                        p.OpenDoorData=qrCodeIds[0];
                        DoorMasterSdkUtil.openDoor(p,BtnList[0],0);
                      }
                  }else{
					  var qrCodeIds_=JSON.stringify(qrCodeIds);
					this_.$route.push({name:'ele_call',query:qrCodeIds_})
                    
                    

                  }
                }
              }
            })
          }
        }
      }
    }
	},
	openQrCodeScaner(){
		var this_=this;
		if(window){

			var parent=window.parent;
			if(parent){
			
  //console.log("1111");
  if (parent.app) {
    parent.app.OtherUtil.openQrCodeScanner(
      parent,
      function (qrCodeScanResultData) {
		console.log(qrCodeScanResultData);
	
        parent.app.DataProcessUtil.processQrCodeScanResultData(
          parent,
          qrCodeScanResultData,
          function (returnData) {
            this_.SaveDataAndGo(returnData);
            
          }
        );
      }
    );
  }
}
			}
		

	},
    gopage(){
      // this.$router.push({name:'page2'})
    }
  }
};
</script>

